<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <div class="card-body">
    <section class="mb-4">
      <!-- type -->
      <div class="mb-4">
        <h5>TYPE：</h5>
        <template v-for="(item, index) in types" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-type"
            :id="`type${index}`"
            autocomplete="off"
            :value="item"
            v-model="option.type"
            @click="option.quantity = 1;"
          />
          <label class="btn border-0 position-relative me-4" :for="`type${index}`"
            >{{ item }}
            <CircleTwo :class="{ 'd-none': option.type !== item }" />
          </label>
        </template>
      </div>
      <!-- flavor -->
      <div class="mb-4">
        <h5>FLAVOR：</h5>
        <template v-for="(item, index) in productList" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-flavor"
            :id="`flavor${index}`"
            autocomplete="off"
            :value="item.title"
            v-model="option.flavor"
            @click="tempProduct = item; option.quantity = 1;"
          />
          <label
            class="btn border-0 position-relative"
            v-if="item.category === option.type"
            :for="`flavor${index}`"
            >{{ item.title }}
            <CircleOne :class="{ 'd-none': item.title !== option.flavor }" />
          </label>
        </template>
      </div>
      <!-- size -->
      <div class="mb-4">
        <h5>SIZE：</h5>
        <template v-for="(item, index) in productList" :key="index">
          <template v-for="(data, key) in item.options" :key="key">
            <input
              type="radio"
              class="btn-check me-md-4"
              v-if="item.title === option.flavor"
              name="options-size"
              :id="`size${key}`"
              autocomplete="off"
              :value="data._id"
              v-model="option.select"
              @click="
                option.price = data.price;
                option.quantity = 1;
              "
            />
            <label
              class="btn border-0 position-relative me-3"
              v-if="item.title === option.flavor"
              :for="`size${key}`"
              >{{ data.size }}
              <CircleThree :class="{ 'd-none': data._id !== option.select }" />
            </label>
          </template>
        </template>
      </div>
      <!-- price -->
      <div class="mb-4">
        <h5>PRICE：</h5>
        <p class="ms-3">
          $ <span class="text-secondary">{{ option.price }}</span> / EACH
        </p>
      </div>
      <!-- 數量＆＄ -->
      <div class="mb-4">
        <h5 class="text-uppercase">quantity：</h5>
        <nav aria-label="..." class="me-4">
          <ul class="pagination">
            <li class="page-item">
              <a
                class="page-link rounded-0"
                href="#"
                @click.prevent="option.quantity--"
                :class="{ disabled: option.quantity <= 1 }"
                >-</a
              >
            </li>
            <li class="page-item">
              <div class="page-link">{{ option.quantity }}</div>
            </li>
            <li class="page-item">
              <a
                class="page-link rounded-0"
                href="#"
                @click.prevent="option.quantity++"
                :class="{ disabled: option.quantity >= 10 }"
                ><i class="bi bi-plus-lg"></i
              ></a>
            </li>
          </ul>
        </nav>
      </div>
      <!-- 總金額 -->
      <div class="">
        <h5 class="text-uppercase">total：</h5>
        <div class="fst-italic ms-3 h4 text-secondary luluText">
          ${{ this.$toCurrency(option.price * option.quantity) }}
        </div>
      </div>
    </section>
    <button
      class="btn btn-outline-primary rounded-0 mb-3"
      @click="$emit('addCart', { productId: tempProduct._id, option: option})"
    >
      <i class="bi bi-caret-right-fill"></i>Add to Chart
    </button>
  </div>
</template>

<style lang="scss" scoped>
.circle-1 {
  width: 5rem;
}
.circle-2 {
  width: 5rem;
}
.circle-3 {
  width: 5rem;
}
</style>

<script>
import CircleOne from '../models/circle/CircleOne.vue';
import CircleTwo from '../models/circle/CircleTwo.vue';
import CircleThree from '../models/circle/CircleThree.vue';

export default {
  data() {
    return {
      tempProduct: {},
      productList: [],
      flavorList: [],
      types: ['pie', 'cake', 'cookie'],
      option: {
        type: 'pie',
        flavor: '',
        select: '',
        price: 0,
        quantity: 1,
      },
    };
  },
  props: ['products'],
  components: {
    CircleOne,
    CircleTwo,
    CircleThree,
  },
  watch: {
    products() {
      this.productList = this.products;
      this.selectFlavor();
    },
    // eslint-disable-next-line object-shorthand
    'option.type'() {
      this.selectFlavor();
    },
    // eslint-disable-next-line object-shorthand
    'option.flavor'() {
      this.selectSize();
    },
    tempProduct() {
      this.$emitter.emit('infoData', this.tempProduct);
    },
  },
  methods: {
    selectFlavor() {
      this.flavorList = [];
      this.productList.forEach((item) => {
        if (this.option.type === item.category) {
          this.flavorList.push(item);
        }
      });
      this.option.flavor = this.flavorList[0].title;
      // eslint-disable-next-line prefer-destructuring
      this.tempProduct = this.flavorList[0];
    },
    selectSize() {
      this.flavorList.forEach((item) => {
        if (item.title === this.option.flavor) {
          this.sizeList = item.options;
        }
      });
      // eslint-disable-next-line no-underscore-dangle
      this.option.select = this.sizeList[0]._id;
      this.option.price = this.sizeList[0].price;
    },
  },
};
</script>
