<!-- eslint-disable max-len -->
<template>
  <!-- 上半部 -->
  <section class="card-group row w-tab">
    <!-- 詳細商品 -->
    <div class="card col-sm-6 p-0">
      <ProductInformation />
    </div>
    <!-- 購物流程 -->
    <div class="card col-sm-6">
      <ShopProcess :products="productList" @addCart="addCart" />
    </div>
  </section>
  <section class="card-group row">
    <div class="card px-0 border-2">
      <CartList :carts="cartList" @addCart="addCart" @deleteCart="deleteCart" />
    </div>
  </section>
</template>

<script>
import ProductInformation from '@/components/shop/ProductInformation.vue';
import ShopProcess from '@/components/shop/ShopProcess.vue';
import CartList from '@/components/shop/CartList.vue';
// eslint-disable-next-line object-curly-newline
import { apiGetProducts, apiPostCart, apiDeleteCart, apiGetCarts } from '@/assets/javascript/api';

export default {
  data() {
    return {
      productList: [],
      cartList: [],
    };
  },
  components: {
    ShopProcess,
    ProductInformation,
    CartList,
  },
  methods: {
    getProducts() {
      this.$emitter.emit('loadingStatus', true);
      apiGetProducts()
        .then((res) => {
          this.productList = res.data.data;
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    addCart(data) {
      this.$emitter.emit('loadingStatus', true);
      apiPostCart(data)
        .then(() => {
          this.$emitter.emit('loadingStatus', false);
          this.getCarts();
          this.$emitter.emit('getCarts');
        })
        .catch((err) => {
          console.log(err.response.data.message);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    getCarts() {
      this.$emitter.emit('loadingStatus', true);
      apiGetCarts()
        .then((res) => {
          this.cartList = res.data.data;
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err.response.data.message);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    deleteCart(data) {
      this.$emitter.emit('loadingStatus', true);
      apiDeleteCart({ data })
        .then(() => {
          this.$emitter.emit('loadingStatus', false);
          this.$emitter.emit('messageModal', { status: true, message: 'Delete Product Option' });
          this.getCarts();
          this.$emitter.emit('getCarts');
        })
        .catch(() => {
          this.$emitter.emit('loadingStatus', false);
        });
    },
  },
  mounted() {
    this.getProducts();
    this.getCarts();
  },
};
</script>
