<template>
  <section class="card-body p-0">
    <table class="table tableText tableSm table-bordered border-primary m-0">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">PRODUCT</th>
          <th scope="col">SIZE</th>
          <th scope="col">PRICE</th>
          <th scope="col">Qty</th>
          <th scope="col">SUB</th>
        </tr>
      </thead>
      <tbody class="table-group-divider luluText">
        <template v-for="(item, index) in cartList.carts" :key="index">
          <tr>
            <td>{{ index + 1 }}</td>
            <td>
              <div class="d-flex align-items-center text-sm-nowrap">
                <i
                  class="bi bi-circle-fill me-1"
                  :class="{
                    'text-danger': item.product.category === 'pie',
                    'text-info': item.product.category === 'cake',
                    'text-secondary': item.product.category === 'cookie',
                  }"
                  style="font-size: 8px"
                ></i
                >{{ item.product.title }}
              </div>
            </td>
            <template v-for="(data, key) in item.option" :key="key">
              <template v-if="key === 0">
                <td>{{ data.select.size }}</td>
                <td>${{ data.select.price }}</td>
                <td class="text-center">
                  <a
                    href="#"
                    class="me-1 text-decoration-none"
                    @click.prevent="cartStatus({
                        num:data.quantity,
                        productId: item.product._id,
                        option: { select: data.select._id, quantity: -1 },
                      })"
                    >-</a
                  ><span>{{ data.quantity }}</span
                  ><a
                    href="#"
                    class="ms-1 text-decoration-none"
                    @click.prevent="
                      $emit('addCart', {
                        productId: item.product._id,
                        option: { select: data.select._id, quantity: 1 },
                      })
                    "
                    :class="{ 'd-none': data.quantity >= 10 }"
                    >+</a
                  >
                </td>
                <td>${{ this.$toCurrency(data.quantity * data.select.price) }}</td>
              </template>
            </template>
          </tr>
          <template v-for="(data, key) in item.option" :key="key">
            <tr v-if="item.option.length !== 1 && key !== 0">
              <td colspan="2"></td>
              <td>{{ data.select.size }}</td>
              <td>${{ data.select.price }}</td>
              <td class="text-center">
                <a
                  href="#"
                  class="me-1 text-decoration-none"
                  @click.prevent="cartStatus({
                        num:data.quantity,
                        productId: item.product._id,
                        option: { select: data.select._id, quantity: -1 },
                      })"
                  >-</a
                ><span>{{ data.quantity }}</span
                ><a
                  href="#"
                  class="ms-1 text-decoration-none"
                  @click.prevent="
                    $emit('addCart', {
                      productId: item.product._id,
                      option: { select: data.select._id, quantity: 1 },
                    })
                  "
                  :class="{ 'd-none': data.quantity >= 10 }"
                  >+</a
                >
              </td>
              <td>${{ this.$toCurrency(data.quantity * data.select.price) }}</td>
            </tr>
          </template>
        </template>
        <tr>
          <td colspan="2">
            <div class="d-flex align-items-center dashBottom">
              <i class="bi bi-dot h3 text-danger mb-0"></i>
              <h6>pie</h6>
            </div>
            <div class="d-flex align-items-center dashBottom">
              <i class="bi bi-dot h3 text-info mb-0"></i>
              <h6>cake</h6>
            </div>
            <div class="d-flex align-items-center">
              <i class="bi bi-dot h3 text-secondary mb-0"></i>
              <h6>cookie</h6>
            </div>
          </td>
          <td colspan="3" class="align-middle">
            <select class="form-select" aria-label="Default select example">
              <option selected>選擇折扣卷</option>
              <option value="1">Line Friend -100</option>
              <option value="2">BBB -50</option>
              <option value="3">LULU So CUTE</option>
            </select>
            total: ${{ this.$toCurrency(total) }}
          </td>
          <td colspan="1" class="text-sm-end text-center align-middle">
            <router-link to="/shop/checkout" class="border border-2 border-primary p-2"
              ><i class="bi bi-caret-right-fill"></i>CHECKOUT</router-link
            >
          </td>
        </tr>
      </tbody>
    </table>
  </section>
</template>

<style lang="scss" scoped>
.dashBottom {
  margin-bottom: -30px;
}
@media (min-width: 1024px) {
  .tableText {
    font-size: 24px;
  }
}
@media (max-width: 488px) {
  .dashBottom {
    margin-bottom: -15px;
  }
}
@media (max-width: 377px) {
  .tableSm > :not(caption) > * > * {
    padding: 0.25rem 0.25rem;
  }
}
</style>

<script>
export default {
  data() {
    return {
      cartList: [],
      total: 0,
      finalTotal: 0,
    };
  },
  props: ['carts'],
  watch: {
    carts() {
      this.cartList = this.carts;
      this.populateCarts();
    },
  },
  methods: {
    cartStatus(data) {
      if (data.num === 1) {
        this.$emit('deleteCart', data);
      } else {
        this.$emit('addCart', data);
      }
    },
    populateCarts() {
      //   計算總金額
      this.total = 0;
      this.cartList.carts.forEach((item) => {
        item.option.forEach((option) => {
          this.total += option.select.price * option.quantity;
        });
      });
    },
  },
};
</script>
